(function() {
  angular.module('neurology-clinic').config(function($stateProvider, $urlRouterProvider) {
    'ngInject';
    $stateProvider.state('home', {
      url: '/',
      templateUrl: 'app/main/template.html',
      controller: 'MainController',
      controllerAs: 'main'
    });
    return $urlRouterProvider.otherwise('/');
  });

}).call(this);



(function() {
  angular.module('neurology-clinic').controller('MainController', function($rootScope, $scope, $state, $timeout, $interval, $element, $anchorScroll, $location) {
    'ngInject';
    $scope.images = [];
    $scope.smImages = ['https://lh4.googleusercontent.com/-xlrrwoIQtIU/UG7wRYrEHVI/AAAAAAAAGls/7OBbG2d9-UY/s800/neymar05.jpg', 'https://s2.glbimg.com/jqIRreq8QhRdP7m0Isq-Zu96U6Q=/top/i.glbimg.com/og/ig/infoglobo1/f/original/2017/11/22/neymar.jpg', 'https://i.ytimg.com/vi/LdVKJEDHAZA/hqdefault.jpg', 'http://cdn.ofuxico.com.br/img/upload/noticias/2018/02/09/neymar-aparece-com-short-de-papai-noel-na-neve-nota_313342_36.jpg'];
    $scope.mdImages = ['https://i.imgur.com/I4czoCY.png', 'https://images0.minutemediacdn.com/production-platform/912x516/5a58ec5ed3e5f44a50000001.jpeg', 'https://conteudo.imguol.com.br/2012/10/04/com-a-cabeca-verde-de-um-et-neymar-posa-para-o-comercial-de-uma-de-suas-patrocinadoras-1349361445964_956x500.jpg', 'https://www.otvfoco.com.br/wp-content/uploads/2018/01/neyma-1024x538.jpg', 'https://conteudo.imguol.com.br/c/copadomundo/2018/Imagem/e7/2018/06/03/neymar-durante-brasil-x-croacia-1528066952794_v2_900x506.jpg', 'https://images.performgroup.com/di/library/GOAL/77/b8/neymar-real-madrid-psg-champions-league-14022018_17regnam5v2tx1rpny7bfv0ucl.jpg?t=-1773952623&quality=90&w=1280', 'https://img.jovempan.uol.com.br/uploads/2017/06/1590946406-neymar-foi-expulso-na-partida-entre-barcelona-e-malaga-no-ultimo-dia-8.jpg'];
    $scope.lgImages = ['https://media.giphy.com/media/UdoS9OzIS2lgY/giphy.gif'];
    $scope.methods = {
      getRandomImage: function(array) {
        var image;
        image = $scope.methods.getRan(0, array.length - 1);
        return array[image];
      },
      getRan: function(min, max) {
        return Math.floor(Math.random() * (max - min + 1)) + min;
      },
      loadImages: function() {
        console.log($scope.images);
        $scope.images[1] = $scope.methods.getRandomImage($scope.smImages);
        $scope.images[2] = $scope.methods.getRandomImage($scope.smImages);
        $scope.images[3] = $scope.methods.getRandomImage($scope.mdImages);
        $scope.images[4] = $scope.methods.getRandomImage($scope.lgImages);
        $scope.images[5] = $scope.methods.getRandomImage($scope.mdImages);
        return $scope.images[6] = $scope.methods.getRandomImage($scope.mdImages);
      },
      changeImageRan: function() {
        var index, ranImg;
        index = $scope.methods.getRan(1, 6);
        if (index === 1 || index === 2) {
          ranImg = $scope.methods.getRandomImage($scope.smImages);
        } else if (index === 3 || index === 5 || index === 6) {
          ranImg = $scope.methods.getRandomImage($scope.mdImages);
        } else {
          ranImg = $scope.methods.getRandomImage($scope.lgImages);
        }
        return $scope.images[index] = ranImg;
      },
      scrollToHash: function(hash) {
        if (hash) {
          return $('html, body').animate({
            scrollTop: $("#" + hash).offset().top
          }, 1000, 'easeInOutExpo');
        } else {
          return $('html, body').animate({
            scrollTop: $("html").offset().top
          }, 1000, 'easeInOutExpo');
        }
      }
    };
    $scope.methods.loadImages();
    return $interval($scope.methods.changeImageRan, 1500);
  });

  return;

}).call(this);



(function() {
  angular.module('neurology-clinic').run(function($log, $rootScope, $state, $templateCache) {
    'ngInject';
    return $log.debug('runBlock end');
  });

}).call(this);



